const validation = ({name, surname, contacts}) => {
    const error = {
        name: '     4- ',
        surname: '     2-    15- ',
        phone: '     12- ',
        email: 'Email    "@"',
        vk: 'Vk    "@"   '
    }

    if(surname.length <= 3) {
        throw new Error(error.surname)
    }

    if(name.length <= 3) {
        throw new Error(error.name)
    }

    contacts.forEach(contact => {
        if(contact.value === '') {
            if(contact.type.length !== 12) throw new Error('     12- ')
            else return true
        }

        if(contact.value === 'Email') {
            if(contact.type.includes('@') === false) throw new Error(error.email)
            else return true
        }

        if(contact.value === 'Vk') {
            if(contact.type[0] !== '@') throw new Error(error.vk)
            else return true
        }
    })
}

export default validation;
